//加载确认订单信息的模板
const confirmOrderMsg = require('../templates/confirmOrderMsg.ejs');

//用户点击支付时，检查信息是否填写完整
function isComplete() {
    //因为其他地方都是预先填好的，用户只能改不能删，所以只需要验证入住信息与验证方式非在线验证时信息是否为空
    $('.payment-confirm button').click(function () {
	    //******** 提交表单之前进行验证
	    if (!$("#orderForm").myValid()){
	        //将页面回滚到第一个验证不通过的地方
	        //获取第一个验证不通过的元素的位置
	        var failedTest=$('.error')[0].offsetTop-80;
	      
	        $('body, html').animate({
		      	scrollTop:failedTest+"px"
	        },200);
        
	        return;
	    }else{
        //取得form表单的数据
        var formData = $('form').serialize();
        //处理该数据
        var formify = formData.split('&');
        var formObj = {};
        for (var i = 0; i < formify.length; i++) {
          var formSingle = formify[i].split('=');
          formObj[formSingle[0]] = formSingle[1];
        }
        //将加床、加早、加宽带信息加入到formObj中
        formObj['breakfastNum'] = 0;
        formObj['bedNum'] = 0;
        formObj['networkNum'] = 0;
        var addBreakFastMsg = $('.hotel-breakfast-box .hotel-item .hotel-item-left');
        var addBreakFastNum = $('.hotel-breakfast-box .hotel-item .hotel-item-left .add-num');
          for (var breakfast = 0; breakfast < addBreakFastNum.length; breakfast++) {
            formObj['breakfastNum'] += +($(addBreakFastNum[breakfast]).text());
          }
        
        var addBedMsg = $('.hotel-bed-box .hotel-item .hotel-item-left');
        var addBedNum = $('.hotel-bed-box .hotel-item .hotel-item-left .add-num');
          for (var bed = 0; bed < addBedNum.length; bed++) {
            formObj['bedNum'] += +($(addBedNum[bed]).text());
          }
        
        var addNetworkMsg = $('.hotel-network-box .hotel-item .hotel-item-left');
        var addNetworkNum = $('.hotel-network-box .hotel-item .hotel-item-left .add-num');
          for (var network = 0; network < addNetworkNum.length; network++) {
            formObj['networkNum'] += +($(addNetworkNum[network]).text());
          }
        
        
        formObj['addBreakfastMsg'] = addBreakFastMsg.eq(0).text();
        formObj['addBedMsg'] = addBedMsg.eq(0).text();
        formObj['addNetworkMsg'] = addNetworkMsg.eq(0).text();
        for (var j = 1; j < addBreakFastMsg.length; j++) {
          formObj['addBreakfastMsg'] += ';' + addBreakFastMsg.eq(j).text();
        }
        for (var y = 1; y < addBedMsg.length; y++) {
          formObj['addBedMsg'] += ';' + addBedMsg.eq(y).text();
        }
        for (var z = 1; z < addNetworkMsg.length; z++) {
          formObj['addNetworkMsg'] += ';' + addNetworkMsg.eq(z).text();
        }
        
        //个性化要求信息
        formObj['specialReq'] = $("input[name='specialReq']:checked").serialize();
        
        //用户支付总价
        formObj['payTotalMoney'] = +$('#totalPay').text();
        
        formObj['paymentTermName'] = ["客人前台现付", '单结', '周结', '半月结', '月结','不固定', '三日结', '十日结','额度结'];
        
        //入住人信息
        var guestArr = [];
        var guestCollect = $('.guest');
        for (var i = 0; i < guestCollect.length; i++) {
          var surnameCollect = $(guestCollect[i]).find('.first-name');
          var afternameCollect = $(guestCollect[i]).find('.last-name');
          if($(guestCollect[i]).find('.nationality-msg')){
            //国外
            var nationalCollect= $(guestCollect[i]).find('.nationality-msg');
            if( surnameCollect.val().replace(/^\s+|\s+$/g, '')){
              guestArr[guestArr.length] = {};
              //此时数组的长度已经发生变化，所以下面赋值时需-1
              guestArr[guestArr.length-1].surname = $(surnameCollect).val();
              guestArr[guestArr.length-1].aftername = $(afternameCollect).val();
              guestArr[guestArr.length-1].national = $(nationalCollect).val();
            }
          }else{
            //国内
            if( surnameCollect.val().replace(/^\s+|\s+$/g, '')){
              guestArr[guestArr.length] = {};
              guestArr[guestArr.length-1].surname = $(surnameCollect).val();
              guestArr[guestArr.length-1].aftername = $(afternameCollect).val();
            }
          }
        }
    
        formObj['guestArr'] = $(guestArr);
        console.log(formObj);
        //将获取的数据嵌入弹出的确认订单信息框中
        var $confirmOrderMsgStr = $(confirmOrderMsg(formObj));
        
        //用户点击确认之后，隐藏确认订单信息框
        $confirmOrderMsgStr.on('click','.confirm-order',function () {
          $('.confirm-order-msg-box').remove();
          $('.confirm-order-msg').remove();
          
          //进行验价
          //获取参数
          var params = 'staticInfoId=438&adultNum=2&dateNum=2&childrenNum=0&childrenAgeStr=null&isQueryPrice=&supplierId=105&roomId=125067&rateType=2&paymentType=0&paymentTerm=1&willUsedBalance=0&hotelPrice.supplierId=105&hotelPrice.hotelId=438&hotelPrice.roomId=125067&hotelPrice.roomName=overseas&hotelPrice.totalPriceRMB=800.00&hotelPrice.totalNowPriceRMB=0.00&hotelPrice.totalPrice=112.00&hotelPrice.averagePrice=28.00&hotelPrice.taxesAndFees=&hotelPrice.extraPersonFees=&hotelPrice.taxesAndFeesRMB=&hotelPrice.extraPersonFeesRMB=&hotelPrice.cancellationType=&hotelPrice.clause=3_0_0_0_0&hotelPrice.cancellationDesc=%E6%AD%A4%E6%88%BF%E5%8D%B3%E8%AE%A2%E5%8D%B3%E4%BF%9D%EF%BC%8C%E4%B8%80%E4%BD%86%E9%A2%84%E8%AE%A2%E3%80%82%E4%B8%8D%E5%8F%AF%E4%BF%AE%E6%94%B9%E6%88%96%E5%8F%96%E6%B6%88&hotelPrice.currentAlloment=&hotelPrice.breakFastName=%E4%B8%80%E4%BB%BD%E6%97%A9%E9%A4%90&hotelPrice.breakfastPriceBase=&hotelPrice.breakfastPriceRMB=&hotelPrice.internet=&hotelPrice.arriveStartTime=&hotelPrice.arriveEndTime=&hotelPrice.rateType=2&hotelPrice.rateTypeName=%E5%8D%95%E6%97%A9&hotelPrice.breakFastId=8&hotelPrice.paymentType=0&hotelPrice.confirm=true&hotelPrice.supplierAttr=&hotelPriceStrs=%5B%7B%22date%22%3A%222017-09-05%22%2C%22formulaType%22%3A%221%22%2C%22occupancyStock%22%3A2%2C%22price%22%3A28%2C%22salePrice%22%3A200%2C%22nowPrice%22%3A0%2C%22skuId%22%3A153426%2C%22stock%22%3A100%2C%22sellStock%22%3A0%2C%22status%22%3A1%2C%22clauses%22%3A%5B%7B%22createTime%22%3Anull%2C%22createBy%22%3Anull%2C%22updateTime%22%3Anull%2C%22updateBy%22%3Anull%2C%22orderBy%22%3Anull%2C%22limitString%22%3Anull%2C%22andWhereString%22%3Anull%2C%22and_where_string%22%3Anull%2C%22or_where_string%22%3Anull%2C%22clauseId%22%3A3603507%2C%22itemId%22%3A440%2C%22staticInfoId%22%3A438%2C%22skuId%22%3A7198%2C%22clauseType%22%3A1%2C%22clauseNumber%22%3A3%2C%22clauseDate%22%3A0%2C%22clauseTime%22%3A0%2C%22cancelFineType%22%3A0%2C%22cancelFine%22%3Anull%2C%22remark%22%3Anull%2C%22attr%22%3A%22%22%2C%22companyAccountId%22%3Anull%2C%22subAccountId%22%3Anull%2C%22bookBeginDate%22%3Anull%2C%22bookBeginDateStr%22%3Anull%2C%22bookEndDate%22%3Anull%2C%22bookEndDateStr%22%3Anull%2C%22checkInDate%22%3Anull%2C%22checkInDateStr%22%3Anull%2C%22checkOutDate%22%3Anull%2C%22checkOutDateStr%22%3Anull%2C%22formulaType%22%3Anull%2C%22default%22%3Afalse%7D%5D%2C%22nightlyStr%22%3Anull%2C%22discount%22%3Anull%2C%22reserveShow%22%3Anull%7D%2C%7B%22date%22%3A%222017-09-06%22%2C%22formulaType%22%3A%221%22%2C%22occupancyStock%22%3A2%2C%22price%22%3A28%2C%22salePrice%22%3A200%2C%22nowPrice%22%3A0%2C%22skuId%22%3A153427%2C%22stock%22%3A100%2C%22sellStock%22%3A0%2C%22status%22%3A1%2C%22clauses%22%3A%5B%7B%22createTime%22%3Anull%2C%22createBy%22%3Anull%2C%22updateTime%22%3Anull%2C%22updateBy%22%3Anull%2C%22orderBy%22%3Anull%2C%22limitString%22%3Anull%2C%22andWhereString%22%3Anull%2C%22and_where_string%22%3Anull%2C%22or_where_string%22%3Anull%2C%22clauseId%22%3A3603507%2C%22itemId%22%3A440%2C%22staticInfoId%22%3A438%2C%22skuId%22%3A7198%2C%22clauseType%22%3A1%2C%22clauseNumber%22%3A3%2C%22clauseDate%22%3A0%2C%22clauseTime%22%3A0%2C%22cancelFineType%22%3A0%2C%22cancelFine%22%3Anull%2C%22remark%22%3Anull%2C%22attr%22%3A%22%22%2C%22companyAccountId%22%3Anull%2C%22subAccountId%22%3Anull%2C%22bookBeginDate%22%3Anull%2C%22bookBeginDateStr%22%3Anull%2C%22bookEndDate%22%3Anull%2C%22bookEndDateStr%22%3Anull%2C%22checkInDate%22%3Anull%2C%22checkInDateStr%22%3Anull%2C%22checkOutDate%22%3Anull%2C%22checkOutDateStr%22%3Anull%2C%22formulaType%22%3Anull%2C%22default%22%3Afalse%7D%5D%2C%22nightlyStr%22%3Anull%2C%22discount%22%3Anull%2C%22reserveShow%22%3Anull%7D%5D&bedTypeStrs=&startDate=2017-09-05&endDate=2017-09-07&roomNum=2&userNames=fd%23fd%23%E4%B8%AD%E5%9B%BD%2Cfd%23fd%23%E4%B8%AD%E5%9B%BD&surname=fd&userName=fd&countryId=%E4%B8%AD%E5%9B%BD&surname=&userName=&countryId=&surname=fd&userName=fd&countryId=%E4%B8%AD%E5%9B%BD&surname=&userName=&countryId=&specialRequire=&checkType=9&voucherEmail=&voucherFax=&voucherMobile=&payTotalMoney=800.0&toatlBasePrice=112.0&totalNowPrice=0.0'
          
          //发送请求
          const checkThePrice = require('./sendRequest.js').checkThePrice;
          checkThePrice(params,function (data) {
            debugger;
            console.log(data);
          })

          // $.post('/order/orderValidate.do', params, function(data){
          //   debugger;
          // })
        });
        //用户点击取消之后，隐藏确认订单信息框
        $confirmOrderMsgStr.on('click','.cancel-order',function () {
          $('.confirm-order-msg-box').remove();
          $('.confirm-order-msg').remove();
        });
        
        $('.main').append($confirmOrderMsgStr);
		  	return false;
	    }

	    //跳转到确认支付页面
	})
}

module.exports = isComplete;



